import React,{useEffect} from 'react';
import { useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import { Button, Card, CardBody, CardTitle, Row } from 'reactstrap';
import IntlMessages from '../../helpers/IntlMessages';
import { Colxx, Separator } from '../../components/common/CustomBootstrap';
import Breadcrumb from '../../containers/navs/Breadcrumb';
// import RadialProgressCard from '../../components/cards/RadialProgressCard';
import { getCurrentUser } from '../../helpers/Utils';
import { adminRoot } from '../../constants/defaultValues';
// import HackCard from '../../containers/application/HackathonCard';
import { getAllHackathons } from '../../redux/actions';
import RecentHack from '../../components/common/RecentHack';

const Dashboard = ({ match,accessToken,organizerHackathons,getAllHackathonsAction }) => {
  const history = useHistory();
  const { id } = getCurrentUser();
  console.log(id);
  console.log("HI");
  let count = 0;
  useEffect(() => {
    getAllHackathonsAction(accessToken, id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.dashboard" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Row>
        <Colxx xxs="12" md="9" className="mb-4">
          <Card>
            <CardBody>
              <Row className="icon-cards-row">
                <Colxx xxs="6" sm="4" md="4" lg="4">
                  <Card className="mb-4" color="secondary">
                    <CardTitle className="p-4">
                      <p className="lead text-center text-white">
                        <IntlMessages id="user.dashboard.card-1.title" />
                      </p>
                    </CardTitle>
                    <CardBody className="text-center">
                      <p className="text-center">
                        <IntlMessages id="user.dashboard.card-1.desc" />
                      </p>
                      <Button
                        color="primary"
                        onClick={() =>
                          history.push(`${adminRoot}/challenges/explore`)
                        }
                      >
                        <IntlMessages id="user.dashboard.card-1.button" />
                      </Button>
                    </CardBody>
                  </Card>
                </Colxx>

                <Colxx xxs="6" sm="4" md="4" lg="4">
                  <Card className="mb-4" color="secondary">
                    <CardTitle className="p-4">
                      <p className="lead text-center text-white">
                        <IntlMessages id="user.dashboard.card-2.title" />
                      </p>
                    </CardTitle>
                    <CardBody className="text-center">
                      <p className="text-center">
                        <IntlMessages id="user.dashboard.card-2.desc" />
                      </p>

                      <Button
                        color="primary"
                        onClick={() => history.push(`${adminRoot}/profile`)}
                      >
                        <IntlMessages id="Go to setup profile." />
                      </Button>
                    </CardBody>
                  </Card>
                </Colxx>
                <Colxx xxs="6" sm="4" md="4" lg="4">
                  <Card className="mb-4" color="secondary">
                    <CardTitle className="p-4">
                      <p className="lead text-center text-white">
                        <IntlMessages id="user.dashboard.card-3.title" />
                      </p>
                    </CardTitle>
                    <CardBody className="text-center">
                      <p className="text-center">
                        <IntlMessages id="user.dashboard.card-3.desc" />
                      </p>

                      <Button 
                        color="primary"
                        onClick={() => history.push(`${adminRoot}/blank-page`)}
                      >
                        <IntlMessages id="user.dashboard.card-3.button" />
                      </Button>
                    </CardBody>
                  </Card>
                </Colxx>
              </Row>

          

            </CardBody>
          </Card>
        </Colxx>
      </Row>
      {organizerHackathons.map((item) => {
            if(item.name !== undefined && count === 0)
            { count = 1;
              return(
                  
        <Row>
        <Colxx xxs="12">
          <h1>Your hackathons</h1>
          <Separator className="mb-5" />
        </Colxx>
      </Row>
              );
            }
            return null;
          })}

        <Row>
          {organizerHackathons.map((item) => {
            if(item.name !== undefined)
            {
              return(
            <Colxx xxs="12" sm="6" md="4" key={item.id} className="mb-4">
              <RecentHack data={item} participated={item.status !== 0} />
            </Colxx>);
            }
            return null;
          })}
        </Row>
    </>
  );
};

const mapStateToProps = ({ authUser, hackathonData }) => {
  const { accessToken } = authUser;
  const { organizerHackathons, loading, error } = hackathonData;
  return { accessToken, organizerHackathons, loading, error };
};

export default connect(mapStateToProps, {
  getAllHackathonsAction: getAllHackathons,
})(Dashboard);
